let getFirstAttachmentOfDocument = function (idDocument) {
    let document = Documents.findOne(idDocument);
    if (document) {
        let attachment = Attachments.findOne({idDocument: idDocument, isDefault: true});
        if (!attachment) {
            return '';
        } else {
            let baseUrl = process.env.ROOT_URL?process.env.ROOT_URL: Meteor.absoluteUrl.defaultOptions.rootUrl;
            let fullPath = baseUrl + 'upload/' +idDocument + '/' + attachment.new_name;
            console.log(fullPath);
            return fullPath;
        }
    } else {
        return '';
    }

}


let getFirstAttachmentOfDocumentToCompress = function (idDocument) {
    let document = Documents.findOne(idDocument);
    if (document) {
        let attachment = Attachments.findOne({idDocument: idDocument, isDefault: true});
        if (!attachment) {
            return '';
        } else {
            let fullPath = process.env.PWD + '/.uploads/' + idDocument + '/' + attachment.new_name;
            return {
                name: attachment.new_name,
                fullPath: fullPath,
            };
        }
    } else {
        return '';
    }

}

export {getFirstAttachmentOfDocument, getFirstAttachmentOfDocumentToCompress};
