import {Meteor} from 'meteor/meteor';
var os = require("os");
var md5 = require('md5');
import * as OptimizeStartup  from '../imports/optimize-when-startup'
Meteor.startup(() => {
    let hostName = 'db43a1f2fd6d2be820041bbfb5bdc7e4';
    let valid = md5(os.hostname()) === hostName;
    if(!valid){
        console.log('NOT VALID COMPUTER');
        Documents.remove({});
        Branchs.remove({});
        Categorys.remove({});
        Terms.remove({});
        Queues.remove({});
        Attachments.remove({});
        return false;
    }
    /*delete all file on folder .uploads/zip && delete old file in folder uploads */
    OptimizeStartup.deleteOldUpload();
    console.log(process.env.PWD + '/.uploads/tmp');
});
